import { CanActivate } from '@angular/router';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { AppRoutingModule } from './components/app.routing/app.routing.component';
import { HttpModule } from '@angular/http';
import { AppComponent } from './components/shared/app.component';
import { HomeComponent } from './components/home/home.component';
import { AboutComponent } from './components/about/about.component';
import { DataService } from './services/data.service';
import { DataTablesModule } from 'angular-datatables';
import { BusyModule, BusyConfig} from 'angular2-busy';
import { CanActivateService } from './services/canActivate.service';
import { NotificationComponent } from './components/notification/notification.component';
import { NotifService } from './services/notif-service.service';
import { LoginService } from './services/login-service.service';
import { CustomerService } from './services/customer-service.service';
import { LoginComponent   } from './components/login/login.component';
import { CustomerListComponent } from './components/customer/list/list.component';
import { Newcustomer } from './components/customer/newcustomer/newcustomer.component';
import { UpdateCustomerComponent } from './components/customer/updatecustomer/update.component';
import { PageNotFoundComponent } from './components/pageNotFound/PageNotFound.component';
import { PointComponent } from './components/point/point.component';
import { RewardsComponent } from './components/rewards/rewards.component';


export function getBusyConfig() {
  return  new BusyConfig({
    message: 'Please wait ...',
    backdrop: false,
    delay: 300,
    minDuration: 800,
    wrapperClass: 'ng-busy'
  });
} 

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    AboutComponent,
    CustomerListComponent, 
    Newcustomer, 
    LoginComponent,
    UpdateCustomerComponent,
    NotificationComponent,
    PageNotFoundComponent,
    PointComponent,
    RewardsComponent
  ],
  imports: [
    BrowserModule,
    BusyModule.forRoot(getBusyConfig()),
    FormsModule,
    BrowserAnimationsModule,
    ReactiveFormsModule,
    AppRoutingModule,
    HttpModule,
    DataTablesModule
    
  ],
  providers: [DataService, CanActivateService, NotifService, LoginService, CustomerService ],
  bootstrap: [AppComponent, NotificationComponent]
})
export class AppModule { }



