import { NotifService } from './../../../services/notif-service.service';
import { Component, NgModule , OnInit } from '@angular/core';
import { CustomerService } from '../../../services/customer-service.service';
import { Subscription }   from 'rxjs/Subscription';
import { ICustomer } from './../../../models/ICustomer';
import { Subject } from 'rxjs/Rx';
import { RouterLink, Event } from '@angular/router';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
  
})
export class CustomerListComponent implements OnInit {
   listCustomers : any = [];
   filteredCustomers : any = [];
   first : string = "";
   last : string = "";
   selectedItem : number;
   dtTrigger = new Subject();
   dtOptions: DataTables.Settings = {};

   constructor(private _customerService : CustomerService, private notifService : NotifService   )  {
      this.init();
   }

   private init()
   {
      this.dtOptions = {
        pagingType: 'full_numbers',
        pageLength: 10        
      };  

      this.selectedItem = -1;
      this._customerService.getCustomers() .then( response => {
        this.listCustomers = response.json() as ICustomer[];
        this.filteredCustomers = this.listCustomers.slice(0);
        // Calling the DT trigger to manually render the table
        this.dtTrigger.next();
      
      }).catch((resp) => {
        console.log(resp);
        this.notifService.error("Server Exception was raised");
      });
   }
  public searchCustomer ()
  {
    if(this.selectedItem == -1)
    {
        this.filteredCustomers = this.listCustomers.slice(0);
    }else
    {
      this.filteredCustomers = this.listCustomers.filter(
        customer => customer.customerId == this.selectedItem );
    }
  }

  public deleteCustomer(id : number)
  {
    this._customerService.deleteCustomer(id).then( response => {
      this.filteredCustomers = this.filteredCustomers.filter( 
        (item : ICustomer) => {
          return (item.customerId != id)
      })
      this.notifService.success("Delete Operation was well done");
     // this.dtTrigger.next();
    }).catch((resp) => {
         this.notifService.error("Server Exception was raised");
     });
  }
  
  ngOnInit()
  {
  }
 
}



