import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent   } from '../login/login.component';
import { Newcustomer } from '../customer/newcustomer/newcustomer.component';
import { AppComponent } from '../shared/app.component';
import { CustomerListComponent } from '../customer/list/list.component';
import { CanActivateService } from '../../services/canActivate.service';
import { PageNotFoundComponent } from './../pageNotFound/PageNotFound.component';
import { UpdateCustomerComponent } from './../customer/updatecustomer/update.component';
import {HomeComponent} from '../../components/home/home.component';
import {AboutComponent} from '../../components/about/about.component';
import {PointComponent} from '../../components/point/point.component';
import { RewardsComponent } from '../rewards/rewards.component';

const routes: Routes = [
    {
    path: '', component:HomeComponent},
   { path: 'about', component:AboutComponent},
   { path: 'login', component: LoginComponent },
   { path: 'logout', component: LoginComponent },
   { path: 'point', component: PointComponent },
   { path: 'rewards', component: RewardsComponent },
   { path: 'customerlist', component: CustomerListComponent, canActivate: [CanActivateService] },
   { path: 'newcustomer', component: Newcustomer , canActivate: [CanActivateService]},
   { path: 'updatecustomer/:id', component: UpdateCustomerComponent },
   
   // otherwise redirect to home
   { path: '**', component: PageNotFoundComponent }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
   })
export class AppRoutingModule { }



