import { Component,NgModule, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { ICustomer } from "../../../models/ICustomer"
import { CustomerService } from './../../../services/customer-service.service';
import { NotifService } from './../../../services/notif-service.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-updatecustomer',
  templateUrl: './update.component.html',
  styleUrls: ['./update.component.css']
})
export class UpdateCustomerComponent  implements OnInit {
  complexForm : FormGroup ;

  constructor(private fb: FormBuilder
    , private customerService : CustomerService
    , private notifService : NotifService
    , private route: ActivatedRoute ){
    // Here we are using the FormBuilder to build out our form.
   this.route.params.subscribe(params => {

      let id = +params['id']; // (+) converts string 'id' to a number
      this.complexForm = fb.group({
        // We can set default values by passing in the corresponding value or leave blank if we wish to not set the value. For our example, we’ll default the gender to female.
        'customerId' : [""],
        'first' : ["", Validators.required],
        'last': ["", Validators.required],
        'middle' : ["", Validators.required],
       });
 
      this.customerService.getCustomer(id).then(resp => {
        let customer = resp.json() as ICustomer;
 
        this.complexForm = fb.group({
          // We can set default values by passing in the corresponding value or leave blank if we wish to not set the value. For our example, we’ll default the gender to female.
          'customerId' : [customer.customerId],
          'first' : [customer.first, Validators.required],
          'last': [customer.last, Validators.required],
          'middle' : [customer.middle, Validators.required],
         });
    
      }).catch(exp => {
          this.notifService.error("Server Exception was raised");
       }) ;
     });
  }
  
  ngOnInit() {
  }

  public updateCustomer(model: ICustomer){
    console.log(model);
    this.customerService.updateCustomer(model).then(resp => {
      this.notifService.success("Update operation is well done");      
    }).catch(exp => {
        this.notifService.error("Server Exception was raised");
     }) ;
  }
}



